import React from 'react'

const Card = ({name, mid, roomNum, mobileNum, hotelName}) => {
    return (
        <div className="tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5">
            <img src="Mascot.jpg" className="image" alt="robots"></img>
            <h4>{name}</h4>
            <h4>{mid}</h4>
            <h4>{mobileNum}</h4>
            <h4>{roomNum}</h4>
            <h4>{hotelName}</h4>
        </div>
    )
}

export default Card


